.site-navigation__secondary-list {
  @include reset-list;
  background-color: $color--white;
  margin: 0;

  @media (max-width: map-get($breakpoints, 'medium-large--down')) {
    background-color: $color--cararra;
    padding: 0 unit(1);

    &[data-hidden='true'] {
      display: none;
    }

    &[data-hidden='false'] {
      display: block;

      &.-columns-2,
      &.-columns-3 {
        display: flex;
        flex-wrap: wrap;
      }
    }
  }

  @include respond-to('medium-large') {
    @include dropdown-image(50%);
    left: 0;
    min-height: 30rem;
    padding: unit(1.5) unit(2);
    position: absolute;
    right: 0;
    top: 100%;
    z-index: -1;

    html.js &,
    html.js &[data-hidden='true'] {
      opacity: 0;
      transform: translate3d(0, -20px, 0);
      transition: visibility 0s linear 0.3s, opacity 0.3s $ease--snappy,
        transform 0.3s $ease--snappy;
      visibility: hidden;
    }

    html.js &[data-hidden='false'] {
      opacity: 1;
      transform: translate3d(0, 0, 0);
      transition: visibility 0s linear 0s, opacity 0.3s $ease--snappy,
        transform 0.3s $ease--snappy;
      visibility: visible;
    }

    &::before,
    &::after {
      @include pseudo;
      bottom: 0;
      top: 0;
    }

    &::before {
      z-index: 1;
    }

    &::after {
      background-repeat: no-repeat;
      background-size: cover;
      z-index: 0;
    }

    &.-left {
      &::after {
        background-position: left center;
        left: 0;
      }
    }

    &.-right {
      &::after {
        background-position: right center;
        right: 0;
      }
    }

    &.-gradient {
      &.-left::before {
        background: linear-gradient(
          to left,
          $color--white 0%,
          rgba($color--white, 0) 100%
        );
      }

      &.-right::before {
        background: linear-gradient(
          to right,
          $color--white 0%,
          rgba($color--white, 0) 100%
        );
      }
    }

    &.-columns-2,
    &.-columns-3 {
      @include dropdown-image(45%);
      column-count: 2;
    }
  }

  @include respond-to('large') {
    &.-columns-3 {
      @include dropdown-image(40%);
      column-count: 3;
    }
  }
}

.site-navigation__secondary-item {
  break-inside: avoid;
  line-height: 1.4;
  margin: 0;

  @media (max-width: map-get($breakpoints, 'medium-large--down')) {
    border-bottom: 1px solid rgba($color--black, 0.1);
    padding: unit(0.8) 0;

    .-columns-1 & {
      width: 100%;

      &:last-child {
        border-bottom: none;
      }
    }

    .-columns-2 &,
    .-columns-3 & {
      width: 50%;

      &:nth-last-child(-n + 2) {
        border-bottom: none;
      }
    }
  }

  @include respond-to('medium-large') {
    padding: unit(0.5) 0;
  }

  @include respond-to('large') {
    padding-left: unit(0.5);
    padding-right: unit(0.5);
  }
}

.site-navigation__secondary-link {
  @include hover-underline;
  color: $color--tuatara;
  display: block;

  &.featured {
    color: $color--olive-drab;
    font-weight: $font-weight--bold;
  }

  @include respond-to('large') {
    padding-left: unit(0.5);
    padding-right: unit(0.5);
  }
}
