.article-heading {
  @include null-margins;
  border-bottom: 1px solid $border--light;
  margin-bottom: unit(1);
  padding-bottom: unit(1);

  @include respond-to('medium-large') {
    margin-bottom: unit(2);
    padding-bottom: unit(2);
  }

  &.-has-hero {
    display: block;

    @include respond-to('medium-large') {
      display: none;
    }
  }
}

.article-heading__date {
  @include article-heading__date;
}

.article-heading__headline {
  @include article-heading__headline;
}

.article-heading__details {
  @include article-heading__details;
}

.article-heading__author {
  @include article-heading__author;
}
