.site-navigation {
  @include respond-to('medium-large') {
    float: right;
    width: 75%;
  }

  @include respond-to('large') {
    width: 70%;
  }

  @include respond-to('x-large') {
    width: 60%;
  }
}

.site-navigation__list {
  background-color: $color--white;
  left: 0;
  list-style: none;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 100%;
  width: 100%;
  z-index: 100;

  @media (max-width: map-get($breakpoints, 'medium-large--down')) {
    html.js &,
    html.js &[data-hidden='true'] {
      display: none;
    }

    html.js &[data-hidden='false'] {
      display: block;
    }
  }

  @include respond-to('medium-large') {
    background-color: transparent;
    display: flex;
    position: relative;
  }
}
