/*
 * media queries
 * small:         30em
 * medium:        48.75em
 * medium-large:  61.25em
 * large:         75em
 * x-large:       87.5em
 */
$breakpoints: (
  'small--down': (479 / 16) * 1em,
  'small': (480 / 16) * 1em,
  'medium--down': (779 / 16) * 1em,
  'medium': (780 / 16) * 1em,
  'medium-large--down': (979 / 16) * 1em,
  'medium-large': (980 / 16) * 1em,
  'large--down': (1199 / 16) * 1em,
  'large': (1200 / 16) * 1em,
  'x-large--down': (1399 / 16) * 1em,
  'x-large': (1400 / 16) * 1em
);

// main unit
$unit: 2rem;

// grid
$grid-cols: 12;
$grid-gutter: $unit;
$layout-side-padding: $unit;

// container widths (in px)
// will be converted to rem
$widths: (
  'medium': 780,
  'max': 1720
);
