.button {
  @include un-smooth-text;
  appearance: none;
  background-color: transparent;
  border: none;
  border-radius: 2px;
  color: $color--white;
  display: inline-block;
  font: $font-weight--bold 1.4rem/1 $font-family--sans;
  line-height: unit(2.5);
  margin: 0;
  min-width: unit(12.5);
  overflow: hidden;
  padding: 0 unit(1);
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  transition: background 0.2s $ease--snappy, color 0.2s $ease--snappy;
  z-index: 1;

  @include respond-to('small') {
    padding: 0 unit(2);
  }

  &::before,
  &::after {
    @include pseudo;
    border-radius: 2px;
    height: 100%;
    left: 0;
    right: 0;
    z-index: -1;
  }

  &::before {
    background-color: $color--rose-of-sharon;
    top: 0;
  }

  &::after {
    background-color: darken($color--rose-of-sharon, 10);
    top: 100%;
    transition: top 0.3s $ease--snappy;
  }

  @include hover {
    &::after {
      top: 0;
    }
  }

  &.-small {
    font-size: 1.3rem;
    line-height: unit(1.5);
    min-width: 0;
    padding: 0 unit(1);
  }
}

@mixin input-styles {
  appearance: none;
  background-color: $color--cararra;
  border: none;
  border-radius: 2px;
  box-shadow: inset 1px 2px 3px rgba($color--black, 0.2) !important;
  color: $color--tuatara;
  font: $font-weight--normal 16px/1 $font-family--sans;
  margin: 0;
  padding: 0 20px;
  width: 100%;

  &[aria-invalid='true'] {
    background-color: rgba($color--rose-of-sharon, 0.1);
    border: 2px solid $color--rose-of-sharon;
  }

  [data-whatintent='mouse'] &:focus {
    box-shadow: inset 1px 2px 3px rgba($color--black, 0.2) !important;
  }
}

input[type='email'],
input[type='number'],
input[type='search'],
input[type='text'] {
  @include input-styles;
  height: unit(2);
}

textarea {
  @include input-styles;
  line-height: 1.3;
  padding: 10px 20px;
  resize: vertical;
}

select {
  appearance: none;
  background: $color--cararra url(#{$path}/images/arrow-select--down.svg)
    no-repeat;
  background-position: calc(100% - #{unit(0.75)}) 50%;
  border: none;
  border-radius: 2px;
  box-shadow: inset 1px 2px 3px rgba($color--black, 0.2);
  color: $color--venice-blue;
  display: block;
  font: $font-weight--bold 1.3rem/1 $font-family--sans;
  height: unit(2);
  margin: 0;
  padding: 0 3rem 0 unit(0.5);
  width: 100%;

  &[aria-invalid='true'] {
    background-color: rgba($color--rose-of-sharon, 0.1);
    border: 2px solid $color--rose-of-sharon;
  }

  [data-whatintent='mouse'] &:focus {
    box-shadow: inset 1px 2px 3px rgba($color--black, 0.2) !important;
  }

  // hide arrow in IE
  &::-ms-expand {
    display: none;
  }
}

/*
 * gravity forms styles
 */

.gform_wrapper {
  padding-bottom: unit(1);
}

.gform_body {
  @include null-margins;
}

.gform_fields {
  @include reset-list;
  margin: 0;
}

.gfield {
  + .gfield {
    margin-top: unit(0.8);
  }
}

.gfield_visibility_visible {
  line-height: 1;

  &[label-is] {
    padding-top: unit(1.2);
    position: relative;
  }
}

.gfield_label {
  color: $color--jelly-bean;
  display: block;
  font-size: 1.3rem;
  font-weight: $font-weight--bold;
  line-height: unit(1);

  .gfield_error & {
    color: $color--fire;
  }

  .gfield_error[label-is='floated'] & {
    color: $color--jelly-bean;
  }

  [label-is] & {
    left: unit(1);
    position: absolute;
    top: unit(1.8);
    transition: all 0.2s $ease--snappy;
  }

  [label-is='floated'] & {
    left: 0;
    top: 0;
  }
}

.validation_error {
  background-color: rgba($color--rose-of-sharon, 0.1);
  border-radius: 2px;
  color: $color--fire;
  font-size: 1.3rem;
  font-weight: $font-weight--bold;
  line-height: 1.2;
  padding: unit(0.5);
  text-align: center;
}

.gfield_description {
  font-size: 1.3rem;
  margin-top: unit(0.5);
}

.gform_footer {
  margin: unit(1) 0 0;
  text-align: right;
}

.gform_button {
  background-color: $color--rose-of-sharon;

  @include hover {
    background-color: darken($color--rose-of-sharon, 10);
  }
}
