.hero {
  height: calc(80vh - #{unit(4)});
  margin: 0 auto;
  max-width: width('max');
  position: relative;
  z-index: 0;

  &.-country-page {
    height: calc(80vh - #{unit(8)});
  }

  @include respond-to('small') {
    min-height: 46rem;

    &.-home-page {
      height: 54vw;
      max-height: 93rem;
    }

    &.-country-page {
      height: 40vw;
      max-height: 72rem;
    }

    &.-page {
      height: 28vw;
      max-height: 50rem;
      min-height: 30rem;
    }

    &.-single,
    &.-story-page {
      height: 46vw;
      max-height: 75rem;
    }
  }
}

@keyframes hero-zoom {
  from {
    transform: scale(1.15);
  }

  to {
    transform: scale(1);
  }
}

.hero__image__wrapper,
.hero__image,
.hero__placeholder {
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
}

.hero__image__wrapper {
  overflow: hidden;
  z-index: 1;
}

.hero__image {
  animation: hero-zoom 12s ease-out 0.2s forwards;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transform: scale(1.15);
  z-index: 2;

  &::after {
    @include pseudo;
    background: linear-gradient(
      to bottom,
      rgba($color--tuatara, 0) 50%,
      rgba($color--tuatara, 0.8) 95%
    );
    bottom: 0;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 3;

    .-single & {
      @media (max-width: map-get($breakpoints, 'medium-large--down')) {
        display: none;
      }

      @include respond-to('medium-large') {
        background: linear-gradient(
          to bottom,
          rgba($color--tuatara, 0) 40%,
          rgba($color--tuatara, 0.8) 90%
        );
      }
    }

    .-story-page & {
      background: linear-gradient(
        180deg,
        $color--white 10%,
        rgba($color--white, 0) 90%
      );
    }

    .-country-page & {
      display: none;
    }
  }
}

.hero__placeholder {
  animation: hero-zoom 12s ease-out 0.2s forwards;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transform: scale(1.15);
  z-index: 1;
}

.hero__content-wrapper {
  color: $color--white;
  display: flex;
  height: 100%;
  position: relative;
  z-index: 2;

  .-home-page & {
    align-items: flex-end;
  }

  .-page & {
    align-items: flex-end;
    justify-content: center;
  }

  .-single & {
    align-items: flex-end;
    justify-content: center;
    padding: 0 unit(1);

    @media (max-width: map-get($breakpoints, 'medium-large--down')) {
      display: none;
    }
  }

  .-story-page & {
    color: $color--tuatara;
    justify-content: center;
    padding: 0 unit(1);
  }
}

.hero__content {
  @include null-margins;
  padding: unit(1);

  .-home-page & {
    @include respond-to('medium') {
      width: 40%;
    }

    @include respond-to('medium-large') {
      padding: unit(1) unit(1) unit(2) unit(2);
    }

    @include respond-to('large') {
      padding-left: unit(3);
    }

    @include respond-to('x-large') {
      width: 30%;
    }
  }

  .-single &,
  .-story-page & {
    padding: unit(2) 0 unit(1);
    width: 100%;

    @include respond-to('medium-large') {
      width: 66.66667%;
    }
  }
}

.hero__date {
  @include article-heading__date;
  opacity: 0.8;
}

.hero__headline {
  .-home-page & {
    font: 700 3rem/1.2 $font-family--serif;
  }

  .-single & {
    @include article-heading__headline;
    @include smooth-text;
  }

  .-page &,
  .-story-page & {
    text-align: center;
  }
}

.hero__description {
  .-home-page & {
    font-size: 1.3rem;
    line-height: 1.4;
  }

  .-story-page & {
    font: 1.6rem/1.75 $font-family--serif;

    @include respond-to('medium-large') {
      font: 1.8rem/1.75 $font-family--serif;
    }
  }
}

.hero__details {
  @include article-heading__details;
}

.hero__details__author {
  @include article-heading__author;
  @include smooth-text;
  color: $color--white;
}

.hero__cta {
  @include arrow-link;
  color: $color--white;
}
