@mixin article-heading__date {
  font-size: 1.2rem;
  font-weight: $font-weight--bold;

  @include respond-to('medium-large') {
    font-size: 1.4rem;
  }
}

@mixin article-heading__headline {
  font: 400 2.4rem/1.3 $font-family--serif;
  margin: 0 0 unit(0.8);
  text-align: left;

  @include respond-to('medium-large') {
    font-size: 3.6rem;
  }
}

@mixin article-heading__details {
  @include respond-to('medium-large') {
    align-items: flex-end;
    display: flex;
    width: 100%;
  }

  .social-share {
    @media (max-width: map-get($breakpoints, 'medium-large--down')) {
      display: none;
    }
  }
}

@mixin article-heading__author {
  color: $color--jelly-bean;
  flex-grow: 1;
  font: 2rem/1.2 $font-family--serif;
  margin: 0;
  padding-right: unit(1.5);
}
