.stat {
  display: flex;
  margin: 0;
  padding: 0;
}

.stat__wrapper {
  flex-shrink: 0;
  padding-right: unit(1);
  text-align: right;
  width: 10rem;

  @include respond-to('medium-large') {
    text-align: left;
    width: auto;

    .-stacked & {
      text-align: right;
      width: 10rem;
    }
  }
}

.stat__number {
  display: block;
  font-family: $font-family--kapra-condensed;
  font-size: 5rem;
  line-height: 1;
}

.stat__modifier {
  display: block;
  font-family: $font-family--kapra-expanded;
  font-size: 1.6rem;
  line-height: 1;
  text-transform: uppercase;
}

.stat__caption {
  font-size: 1.3rem;
  line-height: unit(1);
}

.stats-list {
  @include reset-list;

  li {
    margin: 0;

    + li {
      margin-top: unit(2);
    }
  }
}
