@mixin dropdown-image($width) {
  &::before {
    width: $width/3;
  }

  &::after {
    width: $width;
  }

  &.-left {
    padding-left: $width;

    &::before {
      left: ($width/3) * 2;
    }
  }

  &.-right {
    padding-right: $width;

    &::before {
      right: ($width/3) * 2;
    }
  }
}
