.site-navigation__item {
  margin: 0;

  @media (max-width: map-get($breakpoints, 'medium-large--down')) {
    // easy way to remove bottom border of proceeding link
    &.-search {
      margin-top: -1px;
      position: relative;
    }
  }

  @include respond-to('medium-large') {
    flex-grow: 1;
    padding: 0 unit(0.25);

    &:first-child {
      padding-left: 0;
    }

    // search
    &:last-child {
      flex-grow: 0;
      padding-left: 0;
      padding-right: 0;
      position: static;
    }
  }
}

.site-navigation__link {
  color: $color--fuscous-gray;
  display: block;
  font: $font-weight--bold 1.8rem/1 $font-family--sans;
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  white-space: nowrap;
  width: 100%;

  @media (max-width: map-get($breakpoints, 'medium-large--down')) {
    padding: 0 unit(1) 1px;
    position: relative;
    text-align: left;

    &::before {
      @include pseudo;
      background-color: $border--light;
      bottom: 0;
      height: 1px;
      left: unit(1);
      right: unit(1);
    }
  }

  @include respond-to('medium-large') {
    font-size: 1.3rem;
    padding: 0 unit(0.25);
    text-align: center;
    transition: all 0.2s $ease--snappy;

    &:focus,
    &:hover,
    &[aria-expanded='true'] {
      color: $color--jelly-bean;

      .site-navigation__link__shrinkwrap::before {
        border-color: $color--jelly-bean;
      }

      .site-navigation__link__shrinkwrap::after {
        background-color: $color--jelly-bean;
      }
    }

    &:focus,
    &:hover,
    &[aria-expanded='true'],
    .-current & {
      .site-navigation__link__shrinkwrap::after {
        width: 100%;
      }
    }
  }

  &[aria-expanded='true'] {
    .site-navigation__link__shrinkwrap::before {
      margin-top: -5px;
      transform: rotate(225deg);

      @include respond-to('medium-large') {
        margin-top: -2px;
      }
    }

    @media (max-width: map-get($breakpoints, 'medium-large--down')) {
      &::before {
        display: none;
      }
    }
  }
}

// link underline
.site-navigation__link__shrinkwrap {
  display: block;
  line-height: unit(3);
  position: relative;

  // link underline
  @include respond-to('medium-large') {
    display: inline-block;
    line-height: unit(4);

    &::after {
      @include pseudo;
      bottom: 0;
      height: 3px;
      left: 50%;
      pointer-events: none;
      transform: translate3d(-50%, 0, 0);
      transition: all 0.3s $ease--snappy 0.1s;
      width: 0;

      .-current & {
        background-color: $color--fuscous-gray;
      }
    }
  }
}

button.site-navigation__link {
  @include reset-button;

  @media (max-width: map-get($breakpoints, 'medium-large--down')) {
    padding: 0 unit(1);
  }

  // link caret
  .site-navigation__link__shrinkwrap {
    position: relative;

    &::before {
      @include pseudo;
      border-color: $color--tuatara;
      border-style: solid;
      border-width: 0 3px 3px 0;
      height: 10px;
      margin-top: -8px;
      right: 2px;
      top: 50%;
      transform: rotate(45deg);
      transition: all 0.3s $ease--snappy;
      width: 10px;
    }

    @include respond-to('medium-large') {
      padding-right: unit(0.8);

      &::before {
        border-width: 0 2px 2px 0;
        height: 6px;
        margin-top: -4px;
        right: 2px;
        width: 6px;
      }
    }
  }
}
