.site-header {
  background-color: $color--white;
  position: relative;
  z-index: 100;
}

.site-header__heading {
  @include hide-text;
  background: url('#{$path}/images/logo.svg') no-repeat center;
  background-size: contain;
  display: block;
  float: left;
  height: 36px;
  margin: 0;
  transform: translateY(25px);
  width: 197px;

  @include respond-to('large') {
    height: 46px;
    transform: translateY(unit(1));
    width: 267px;
  }
}

.site-header__inner {
  height: unit(4);
  position: relative;

  &::after {
    @include pseudo;
    background-color: rgba($color--tuatara, 0.1);
    bottom: 0;
    height: 1px;
    left: unit(1);
    position: absolute;
    width: calc(100% - #{unit(2)});

    @include respond-to('medium-large') {
      left: unit(3);
      width: calc(100% - #{unit(6)});
    }
  }
}
