.layout-container {
  @include clearfix;
  @include null-margins;
  margin: 0 auto;
  max-width: width('max');
  padding: unit(2) $layout-side-padding;

  @include respond-to('medium-large') {
    padding-bottom: unit(3);
    padding-top: unit(3);
  }

  // if a layout container is the first element, reduce the top padding
  main > &:first-child {
    padding-top: unit(2);
  }

  + .layout-container:not(.-light-gray):not(.-dark-gray) {
    padding-top: 0;
  }

  &.-inset {
    margin: unit(1);
    max-width: calc(#{width('max')} - #{unit(2)});

    @media (min-width: 107.5em) {
      margin-left: auto;
      margin-right: auto;
    }
  }

  &.-padding-none {
    padding-bottom: 0 !important;
    padding-top: 0 !important;
  }

  &.-light-gray {
    background-color: $color--cararra;
    padding-bottom: unit(2);
    padding-top: unit(2);

    + .layout-container.-light-gray {
      padding-top: 0;
    }

    + .layout-container:not(.-light-gray) {
      padding-top: unit(2);

      @include respond-to('medium-large') {
        padding-top: unit(3);
      }
    }
  }

  &.-dark-gray {
    @include smooth-text;
    background-color: $color--fuscous-gray;
    color: #fff;

    + .layout-container:not(.-dark-gray) {
      padding-top: unit(2);

      @include respond-to('medium-large') {
        padding-top: unit(3);
      }
    }
  }
}

/*
 * set tighter wysiwyg container spacing relative to particular other flexible content blocks
 */

.wysiwyg-block__container {
  + .bio-list__container,
  + .callout-button-block__container,
  + .two-column-list-block__container {
    margin-top: unit(-1);

    @include respond-to('medium-large') {
      margin-top: unit(-2);
    }
  }
}
