/*
 * Bocaburger menu button
 * via: https://jonsuh.com/hamburgers/
 */

$bocaburger-layer-width: 26px;
$bocaburger-layer-height: 3px;
$bocaburger-layer-spacing: 4px;
$bocaburger-hover-opacity: 0.7;

.site-navigation__bocaburger {
  @include reset-button;
  display: block;
  overflow: visible;
  padding: 10px 7px;
  position: absolute;
  right: unit(1);
  top: unit(1);

  @include respond-to('medium-large') {
    display: none;
  }
}

.bocaburger-box {
  display: block;
  height: $bocaburger-layer-height * 3 + $bocaburger-layer-spacing * 2;
  position: relative;
  width: $bocaburger-layer-width;
}

.bocaburger-inner {
  display: block;
  margin-top: $bocaburger-layer-height / -2;
  top: 50%;

  &,
  &::before,
  &::after {
    background-color: $color--venice-blue;
    height: $bocaburger-layer-height;
    position: absolute;
    transition: transform 0.15s ease;
    width: $bocaburger-layer-width;
  }

  &::before,
  &::after {
    content: '';
    display: block;
  }

  &::before {
    top: ($bocaburger-layer-spacing + $bocaburger-layer-height) * -1;
  }

  &::after {
    bottom: ($bocaburger-layer-spacing + $bocaburger-layer-height) * -1;
  }
}

.bocaburger-inner {
  bottom: 0;
  top: auto;
  transition: all 0.13s 0s cubic-bezier(0.55, 0.055, 0.675, 0.19);

  &::after {
    top: ($bocaburger-layer-spacing * 2 + $bocaburger-layer-height * 2) * -1;
    transition: top 0.2s 0.2s cubic-bezier(0.33333, 0.66667, 0.66667, 1),
      opacity 0s linear;
  }

  &::before {
    transition: top 0.1s 0.2s cubic-bezier(0.33333, 0.66667, 0.66667, 1),
      transform 0.13s cubic-bezier(0.55, 0.055, 0.675, 0.19);
  }

  [aria-expanded='true'] & {
    transform: translate3d(
        0,
        ($bocaburger-layer-spacing + $bocaburger-layer-height) * -1,
        0
      )
      rotate(-45deg);
    transition-delay: 0.22s;
    transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);

    &::after {
      opacity: 0;
      top: 0;
      transition: top 0.2s cubic-bezier(0.33333, 0, 0.66667, 0.33333),
        opacity 0s 0.22s linear;
    }

    &::before {
      top: 0;
      transform: rotate(90deg);
      transition: top 0.1s 0.15s cubic-bezier(0.33333, 0, 0.66667, 0.33333),
        transform 0.13s 0.22s cubic-bezier(0.215, 0.61, 0.355, 1);
    }
  }
}
