@mixin ui-block--open {
  opacity: 1;
  visibility: visible;

  @include respond-to('medium-large') {
    transition: visibility 0s $ease--snappy 0s, opacity 0.2s;
  }
}

.site-navigation__ui-block {
  background-color: rgba($color--rangoon-green, 0.6);
  bottom: 0;
  display: block;
  left: 0;
  opacity: 0;
  position: fixed;
  right: 0;
  top: 0;
  visibility: hidden;
  z-index: 80;

  @include respond-to('medium-large') {
    transition: visibility 0s $ease--snappy 0.2s, opacity 0.2s;
  }

  &[data-is='open'] {
    @media (max-width: map-get($breakpoints, 'medium-large--down')) {
      &.-mobile {
        @include ui-block--open;
      }
    }

    @include respond-to('medium-large') {
      &.-menu,
      &.-search {
        @include ui-block--open;
      }
    }
  }
}
