.site-search__trigger {
  @include reset-button;
  border: none;
  display: block;
  position: relative;

  @media (max-width: map-get($breakpoints, 'medium-large--down')) {
    color: $color--jelly-bean;
    font: $font-weight--bold 1.8rem/1 $font-family--sans;
    height: unit(3);
    padding: 0 unit(1);
    text-align: left;
    text-transform: uppercase;
    width: 100%;
  }

  @include respond-to('medium-large') {
    @include hide-text;
    height: unit(4);
    width: unit(2);
  }

  &::before,
  &::after {
    @include pseudo;
    top: 50%;
    transition: all 0.2s $ease--snappy;
  }

  &::before {
    border: 3px solid $color--jelly-bean;
    border-radius: 50%;
    height: 16px;
    margin-top: -11px;
    right: unit(1) + 0.5rem;
    width: 16px;

    @include respond-to('medium-large') {
      left: 8px;
      right: auto;
    }
  }

  &::after {
    background-color: $color--jelly-bean;
    height: 3px;
    margin-top: 4px;
    right: unit(1);
    transform: rotate(45deg);
    width: 10px;

    @include respond-to('medium-large') {
      left: 20px;
      right: auto;
    }
  }

  &[aria-expanded='true'] {
    &::before,
    &::after {
      height: 3px;
      margin: -1px 0 0 -11px;
      right: unit(1);
      top: 50%;
      width: 22px;

      @include respond-to('medium-large') {
        left: 50%;
        right: auto;
      }
    }

    &::before {
      background-color: $color--jelly-bean;
      border-radius: 0;
      border-width: 0;
      transform: rotate(-45deg);
    }
  }
}

.site-search {
  @media (max-width: map-get($breakpoints, 'medium-large--down')) {
    background-color: $color--cararra;
    padding: unit(1);

    html.js &,
    html.js &[data-hidden='true'] {
      display: none;
    }

    html.js &[data-hidden='false'] {
      display: block;
    }
  }

  @include respond-to('medium-large') {
    @include dropdown-image(45%);
    background-color: $color--white;
    left: 0;
    min-height: 30rem;
    padding: unit(2);
    position: absolute;
    top: 100%;
    width: 100%;
    z-index: 100;

    html.js &,
    html.js &[data-hidden='true'] {
      opacity: 0;
      transform: translate3d(0, -20px, 0);
      transition: visibility 0s linear 0.3s, opacity 0.3s $ease--snappy,
        transform 0.3s $ease--snappy;
      visibility: hidden;
    }

    html.js &[data-hidden='false'] {
      opacity: 1;
      transform: translate3d(0, 0, 0);
      transition: visibility 0s linear 0s, opacity 0.3s $ease--snappy,
        transform 0.3s $ease--snappy;
      visibility: visible;
    }

    &::before,
    &::after {
      @include pseudo;
      bottom: 0;
      top: 0;
    }

    &::after {
      background-position: left center;
      background-repeat: no-repeat;
      background-size: cover;
      left: 0;
      z-index: 0;
    }

    &.-gradient {
      &::before {
        background: linear-gradient(
          to left,
          $color--white 0%,
          rgba($color--white, 0) 100%
        );
        z-index: 1;
      }
    }
  }

  @include respond-to('large') {
    @include dropdown-image(40%);
  }
}

.site-search__form {
  height: unit(2);
  margin-bottom: unit(2);
  position: relative;

  button {
    @include reset-button;
    @include arrow-link('orange');
    color: $color--rose-of-sharon;
    font-weight: $font-weight--bold;
    height: unit(1);
    position: absolute;
    right: 10px;
    top: 10px;

    @include hover {
      text-decoration: none;
    }
  }

  input[type='search'],
  input[type='text'] {
    border-width: 1px;
    height: unit(2);
    padding-right: 9rem;
    width: 100%;

    @include placeholder {
      color: $color--venice-blue;
      font-size: 1.3rem;
      font-weight: $font-weight--bold;
      opacity: 1;
    }

    &::-webkit-input-placeholder {
      line-height: unit(1);
    }

    @media (max-width: map-get($breakpoints, 'medium-large--down')) {
      background-color: $color--white;
    }
  }
}

.site-search__heading {
  margin: 0;
}

.site-search__suggestions {
  @include reset-list;
  margin: 0;

  @include respond-to('medium-large') {
    column-count: 2;
  }

  a {
    @include hover-underline;
    color: $color--tuatara;
    display: block;
    font-size: 1.4rem;
    line-height: 1.4;
    padding: unit(0.8) 0;

    @include respond-to('medium-large') {
      padding: unit(0.5) 0;
    }
  }

  li {
    margin: 0;

    @media (max-width: map-get($breakpoints, 'medium-large--down')) {
      + li {
        border-top: 1px solid rgba($color--black, 0.1);
      }
    }

    @include respond-to('medium-large') {
      break-inside: avoid;
    }
  }
}
