.u-grid--row {
  @include null-margins;
  list-style: none; // for lists used as grid structure
  padding: 0;

  + .u-grid--row:not(.-no-gutter) {
    margin-top: $grid-gutter;
  }

  @include respond-to('medium-large') {
    display: flex;
    margin-left: $grid-gutter * -1;

    &.-no-gutter {
      margin-left: 0;
    }

    &.-reverse {
      flex-direction: row-reverse;
    }
  }

  > [class*='u-grid--'] {
    + [class*='u-grid--'] {
      margin-top: unit(1);

      @include respond-to('medium-large') {
        margin-top: 0;
      }
    }
  }

  &.-no-gutter {
    > [class*='u-grid--'] {
      margin-top: 0;
    }
  }
}

%grid-item {
  @include null-margins;
  display: block;

  @include respond-to('medium-large') {
    margin: 0;
    min-height: 1px;
    padding-left: $grid-gutter;

    &.-readable {
      padding-left: $grid-gutter + unit(2);
      padding-right: unit(2);
    }

    .-no-gutter & {
      padding-left: 0;
    }
  }
}

@for $i from 1 through $grid-cols {
  $percent: ((100 / $grid-cols) * 1%) * $i;

  .u-grid--#{$i} {
    @extend %grid-item;

    @include respond-to('medium-large') {
      width: $percent;
    }
  }

  .u-grid--offset-#{$i} {
    @include respond-to('medium-large') {
      margin-left: $percent;
    }
  }
}
